@import url(https://fonts.googleapis.com/css2?family=Cinzel&display=swap);
section{background:20%/ 5em;background-image:linear-gradient(76.3deg, #2c3e4e 12.6%, #456783 82.8%);transition:.8s ease-in-out;animation:change 4s ease-in-out infinite alternate;height:100vh;display:flex;justify-content:center;align-items:center}.construction-card{display:flex;flex-direction:column;align-items:center;justify-content:center;width:420px;height:350px;background:rgba(225,225,225,0.1);box-shadow:0 15px 35px rgba(0,0,0,0.2);border-radius:15px;-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);border-top:1px solid rgba(0,0,0,0.2);border-left:1px solid rgba(0,0,0,0.2);animation:fade-in 1.2s ease}.construction-card div{font-family:'Cinzel', serif;font-size:40px;text-align:center;display:flex;flex-direction:column;align-items:center;justify-content:center}@keyframes change{0%{background-position:30% 0%}50%{background-position:51% 100%}100%{background-position:30% 0%}}@keyframes blinds{to{background-image:linear-gradient(90deg, #f90 100%, #444 0)}}@keyframes fade-in{0%{opacity:0}100%{opacity:1}}

